*,
*::before,
*::after {
	box-sizing: border-box;
}

img {
	display: block;
}

.scroll-hidden {
	overflow: hidden;
}

.gallery {
	position: relative;
	z-index: 2;
	padding: 10px;
	flex-flow: row wrap;
	justify-content: space-between;
	transition: all .5s ease-in-out;
	transform: translateZ(0);

	&.pop {
		filter: blur(10px);
	}

	figure {
		// flex-basis: 33.333%;
		padding: 10px;
		overflow: hidden;
		cursor: pointer;

		img {
			width: 100%;
			transition: all .3s ease-in-out;
		}

		figcaption {
			display: none;
		}

	}
}

.popup {
	position: fixed;
	z-index: 100;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background: #fff;
	opacity: 0;
	transition: opacity .5s ease-in-out .2s;


	&.pop {
		opacity: 1;
		transition: opacity .2s ease-in-out 0s;

		figure {
			margin-top: 0;
			opacity: 1;
		}
	}

	figure {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transform-origin: 0 0;
		margin-top: 30px;
		opacity: 0;
		animation: poppy 500ms linear both;

		img {
			position: relative;
			z-index: 2;
			//box-shadow: 0 1px 5px rgba(0, 0, 0, .2), 0 6px 30px rgba(0, 0, 0, .4);
		}

		figcaption {
			position: absolute;
			bottom: 50px;
			background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.78));
			z-index: 2;
			width: 100%;
			padding: 100px 20px 20px 20px;
			color: #fff;
			font-family: 'Open Sans', sans-serif;
			font-size: 32px;

			small {
				font-size: 11px;
				display: block;
				text-transform: uppercase;
				margin-top: 12px;
				text-indent: 3px;
				opacity: .7;
				letter-spacing: 1px;
			}
		}

		.shadow {
			position: relative;
			z-index: 1;
			top: -56px;
			margin: 0 auto;
			background-position: center bottom;
			background-repeat: no-repeat;
			width: 98%;
			height: 50px;
			opacity: .9;
			filter: blur(16px) contrast(1.5);
			transform: scale(0.95, -0.7);
			transform-origin: center bottom;
		}
	}

	.hidden {
		display: none;
	}

	.close {
		position: absolute;
		z-index: 3;
		top: 10px;
		right: 10px;
		width: 25px;
		height: 25px;
		cursor: pointer;
		background: url(#close);
		border-radius: 25px;
		background: rgba(0, 0, 0, .1);
		box-shadow: 0 0 3px rgba(0, 0, 0, .2);

		svg {
			width: 100%;
			height: 100%;
		}
	}
}

