@media(max-width: 767px) {
	.popup.pop figure {
		left: 55%;
		width: 100%;
		max-width: 550px;
		// margin-left: -37.5%;
	}

	.popup.pop img {
		width: 100%;
	}
}

@media(max-width: 576px) {
